.hero__container {
  background-color: getColor('bgGreen');
  background-image: url(../images/desktop/dots_desktop.png);
  background-repeat: no-repeat;
  background-position: 0 100%;
  background-size: contain;
  max-width: 1280px;
  margin: 0 auto;
  padding-bottom: 65px;
  position: relative;

  @media (max-width: 768px) {
    padding-bottom: 12px;
    background-image: url(../images/tablet/dots_tablet.png);
  }

  @media (max-width: 480px) {
    min-height: 315px;
    background-image: url(../images/mobil/dots_mobil.png);
    background-size: 330px;
    background-position: 100% 100%;
  }
}

.hero {
  margin: 0 16px;

  @media (max-width: 768px) {
    margin: 0 32px;
  }
  @media (max-width: 480px) {
    margin: 0 20px;
  }

  &__title {
    width: 370px;
    color: getColor('light');
    font-family: $fontFamilyTitles;
    font-size: 38px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 1.52px;
    text-transform: uppercase;

    @media (max-width: 768px) {
      width: 247px;
      height: 97px;
      font-size: 26px;
      letter-spacing: 1.04px;
    }
  }
  &__box-links {
    margin-bottom: 42px;
    display: inline-flex;
    gap: 34px;

    @media (max-width: 768px) {
      margin-bottom: 12px;
      gap: 24px;
    }
    @media (max-width: 768px) {
      margin-bottom: 12px;
      gap: 16px;
    }
  }

  &__btn--products {
    width: 134px;
    height: 42px;
    cursor: pointer;
    color: #131313;
    font-family: $fontFamilyMain;
    font-size: 14px;
    font-weight: 600;
    background-color: transparent;
    border-radius: 10px;
    border: 2px solid #f8d24e;

    @media (max-width: 768px) {
      width: 108px;
      height: 38px;
      color: #000;
      font-size: 12px;
    }
  }

  &__btn--how-it-is-made {
    cursor: pointer;
    color: getColor('btnDefault');
    font-family: $fontFamilyMain;
    font-size: 14px;
    font-weight: 600;
    background-color: transparent;
    border: none;
    text-decoration-line: underline;

    @media (max-width: 768px) {
      font-size: 12px;
    }
  }

  &__row-ice-cream-plate {
    display: flex;
    gap: 16px;
    align-items: center;
    @media (max-width: 480px) {
      display: none;
    }
  }

  &__box-img-ice-cream-plate {
    width: 225px;
    height: 225px;

    & img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }

    @media (max-width: 768px) {
      width: 152px;
      height: 152px;
    }
  }

  &__box-slogan {
    width: 200px;
    position: relative;
  }

  &__text-slogan {
    color: #fff;
    font-family: DM Sans;
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;

    @media (max-width: 768px) {
      font-size: 12px;
    }
  }

  &__btn-arrow-right {
    cursor: pointer;
    position: absolute;
    bottom: -24px;
    left: 0;
    border: none;
    background-color: transparent;
    fill: white;

    @media (max-width) {
      bottom: -29px;
    }
  }

  &__box-img-ice-cream {
    position: absolute;
    right: 170px;
    bottom: 0;
    z-index: 5;

    @media (max-width: 1280px) {
      right: 8vw;
    }

    @media (max-width: 768px) {
      right: 85px;
    }

    @media (max-width: 480px) {
      right: 45px;
    }
  }
}

.hero__box-info {
  position: absolute;
  top: 0;
  right: 16px;
  display: flex;
  flex-direction: column;
  gap: 20px;

  @media (max-width: 480px) {
    right: 32px;
  }
  @media (max-width: 480px) {
    display: none;
  }
}
.box-info {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.info-number {
  color: getColor('dark');
  text-align: right;
  font-family: $fontFamilyMain;
  font-size: 46px;
  font-style: normal;
  font-weight: 900;
  line-height: normal;
  letter-spacing: 1.84px;
  text-transform: uppercase;
  @media (max-width: 768px) {
    font-size: 28px;
    letter-spacing: 1.12px;
  }
}

.info-text {
  color: getColor('light');
  text-align: right;
  font-family: $fontFamilyMain;
  font-size: 14px;
  font-weight: 400;
  @media (max-width: 768px) {
    font-size: 8px;
  }
}
